import React, {
  ChangeEvent,
  FC,
  useCallback,
  useEffect,
  useRef,
  useState,
} from "react";
import { useHistory } from "react-router-dom";
import { useGetLocale } from "providers";
import { useWindowSize } from "providers";
import clsx from "clsx";

import imgSrc from "../../assets/banners/bonusCode.png";
import imgMobileSrc from "../../assets/banners/bonusCodeMobile.png";

import { useFetch } from "hooks";
import { RedeemResponse } from "pages/ModalPages/pages/Bonuses/components/Code/RedeemCode";
import { Button, notifyBarManager } from "components";
import { DataBlock } from "..";

import css from "./style.module.scss";

interface Props {
  open: (value: string) => void;
}

const BonusCode: FC<Props> = ({ open }) => {
  const intl = useGetLocale();

  const { isMobile } = useWindowSize();

  const [wrap, setWrap] = useState(false);

  const rootRef = useRef<HTMLDivElement>(null);
  const buttonRef = useRef<HTMLDivElement>(null);

  const handleOpenModal = () => {
    open("bonus/bonuscode");
  };
  const [code, setCode] = useState("");
  const [fetch, { data }] = useFetch<RedeemResponse>("bonuscode/redeem");
  const handleRedeem = async () => {
    const response = await fetch({
      variables: {
        bonus_code: code,
      },
    });

    if (response.data) {
      notifyBarManager[response.data.success ? "success" : "error"]({
        timeOut: 4000,
        message: response.data.message,
      });
    }
  };
  const handleSetCode = (event: ChangeEvent<HTMLInputElement>) => {
    setCode(event.target.value.toUpperCase());
  };

  return (
    <div className={css.root} ref={rootRef}>
      <DataBlock
        title={intl.formatMessage({ id: "bonusPage.shitCode.title" })}
        text={intl.formatMessage({ id: "bonusPage.shitCode.text" })}
        callBack={handleOpenModal}
        classList={{
          root: css.rakeRoot,
          wrapper: css.wrapper,
          title: css.title,
          text: css.text,
          link: css.link,
        }}
      />
      <div
        className={clsx(css.buttonWrapper, {
          [css.wrapperWrap]: wrap,
        })}
        ref={buttonRef}>
        <input
          placeholder="Redeem your codes here"
          className={clsx(css.input, {
            [css.wrapInput]: wrap,
          })}
          value={code}
          onChange={handleSetCode}
        />
        <Button className={css.button} onClick={handleRedeem}>
          {intl.formatMessage({
            id: "bonusPage.modals.code.content.redeem.button",
          })}
        </Button>
      </div>
      <img className={css.img} src={isMobile ? imgMobileSrc : imgSrc} alt="" />
    </div>
  );
};

export default BonusCode;
