import React, { FC, useCallback, useEffect, useRef, useState } from "react";
import { useHistory } from "react-router-dom";
import { useGetLocale, useWindowSize } from "providers";

import { DataBlock } from "..";
import css from "./style.module.scss";

interface Props {
  open: () => void;
}

const Rakeback: FC<Props> = ({ open }) => {
  const intl = useGetLocale();

  const rootRef = useRef<HTMLDivElement>(null);
  const overlayRef = useRef<HTMLDivElement>(null);
  const { width, isMobile } = useWindowSize();
  useEffect(() => {
    if (rootRef.current && overlayRef.current) {
      const { width: rootWidth } = rootRef.current.getBoundingClientRect();
      let position = "";
      let сondition = rootWidth < 657 && rootWidth >= 514 && !isMobile;

      if (сondition) {
        position = `calc(100% + ${657 - rootWidth}px)`;
      }
      overlayRef.current.style.backgroundPositionX = position;
    }
    //682
  }, [width]);

  const handleOpenModal = useCallback(() => {
    open();
  }, []);

  return (
    <div className={css.root} ref={rootRef}>
      <DataBlock
        title={intl.formatMessage({ id: "bonusPage.rakeback.title" })}
        text={intl.formatMessage({ id: "bonusPage.rakeback.text" })}
        callBack={handleOpenModal}
        classList={{
          root: css.rakeRoot,
          wrapper: css.wrapper,
          title: css.title,
          text: css.text,
          link: css.link,
        }}
      />

      <div className={css.backgroundOverlay} ref={overlayRef} />
    </div>
  );
};

export default Rakeback;
