import React from "react";
import clsx from "clsx";
import { Helmet } from "react-helmet";

import { useGetLocale } from "providers";
import { Typography } from "components";

import { BetList } from "pages/Home/components/BetsList";

import { Medal, WinBonuses, Level } from "./components";
import { IPushHistoryTo, useModalLocation } from "hooks/useModalLocation";

import Rakeback from "./components/Rakeback";
import BonusCode from "./components/BonusCode";

import css from "./style.module.scss";

const HomePage = () => {
  const intl = useGetLocale();
  const { pushHistoryTo } = useModalLocation("bonus");

  const handleOpenModal = (props: IPushHistoryTo) => {
    pushHistoryTo({ ...props });
  };

  return (
    <>
      <Helmet>
        <title>Bonus | Crypto Gambling Casino</title>
        <meta name="title" content=" Bonus - Pasino.com" />
        <meta
          name="description"
          content="Pasino offers competitive bonuses with rakeback, medals, level system, and much more.  "
        />
        <meta
          name="keywords"
          content="Pasino Bonus, Pasino Rakeback, Pasino Medals, Pasino Level "
        />
        <meta name="robots" content="index, follow" />
        <meta http-equiv="Content-Type" content="text/html; charset=utf-8" />
        <meta name="language" content="English" />
      </Helmet>
      <div className={css.root}>
        <section className={clsx(css.item, css.winBonuses)}>
          <WinBonuses />
        </section>
        <div className={clsx(css.flex, css.firstLine)}>
          <section className={clsx(css.item, css.rakeback)}>
            <Rakeback
              open={() =>
                handleOpenModal({
                  search: { name: "page", value: "bonus" },
                  searchParams: { bonus: "rake" },
                })
              }
            />
          </section>

          <section className={clsx(css.item, css.medal)}>
            <Medal
              open={() =>
                handleOpenModal({
                  search: { name: "page", value: "bonus" },
                  searchParams: { bonus: "medal" },
                })
              }
            />
          </section>
        </div>
        <div className={clsx(css.grid, css.secondLine)}>
          <section className={clsx(css.item, css.shitCode)}>
            <BonusCode
              open={() =>
                handleOpenModal({
                  search: { name: "page", value: "bonus" },
                  searchParams: { bonus: "bonuscode" },
                })
              }
            />
          </section>
          <section className={clsx(css.item, css.level)}>
            <Level />
          </section>
        </div>

        <section className={clsx(css.item)}>
          <Typography className={css.title} variant="h2">
            {intl.formatMessage({ id: "homeScreen.betsRow" })}
          </Typography>
          <BetList />
        </section>
      </div>
    </>
  );
};

export default HomePage;
