import React, { FC, useCallback, useRef } from "react";
import { useGetLocale } from "providers";

import css from "./style.module.scss";

interface Props {}

const WinBonuses: FC<Props> = ({}) => {
  const intl = useGetLocale();

  const rootRef = useRef<HTMLDivElement>(null);

  return (
    <div className={css.root} ref={rootRef}>
      <div className={css.textBlock}>
        <h4 className={css.title}>
          {intl.formatMessage({ id: "bonusPage.winBonuses.title" })}
        </h4>
        <p className={css.text}>
          {intl.formatMessage({ id: "bonusPage.winBonuses.text" })}
        </p>
      </div>
      <div className={css.backgroundOverlay} />
    </div>
  );
};

export default WinBonuses;
