import React, { FC, useCallback, useEffect, useRef, useState } from "react";
import { useHistory } from "react-router-dom";
import { useGetLocale } from "providers";

import { DataBlock } from "..";
import css from "./style.module.scss";

interface Props {}

const Level: FC<Props> = ({}) => {
  const intl = useGetLocale();

  const history = useHistory();

  const rootRef = useRef<HTMLDivElement>(null);

  const handleOpenModal = useCallback(() => {
    history.push("levels");
  }, []);

  return (
    <div className={css.root} ref={rootRef}>
      <DataBlock
        title={intl.formatMessage({ id: "bonusPage.level.title" })}
        text={intl.formatMessage({ id: "bonusPage.level.text" })}
        callBack={handleOpenModal}
        classList={{
          root: css.rakeRoot,
          wrapper: css.wrapper,
          title: css.title,
          text: css.text,
          link: css.link,
        }}
      />
      <div className={css.backgroundOverlay} />
    </div>
  );
};

export default Level;
