import React, { FC, useCallback, useState } from "react";
import { useGetLocale, useWindowSize } from "providers";
import clsx from "clsx";
import medalsImg from "../../assets/banners/medals.png";
import medalsMobileImg from "../../assets/banners/medalsMobile.png";

import { DataBlock } from "..";
import css from "./style.module.scss";

interface Props {
  open: (value: string) => void;
}

const Medal: FC<Props> = ({ open }) => {
  const intl = useGetLocale();
  const { isMobile } = useWindowSize();
  const handleOpenModal = useCallback(() => {
    open("bonus/medal");
  }, []);

  return (
    <div className={clsx(css.root)}>
      <DataBlock
        title={intl.formatMessage({ id: "bonusPage.medals.title" })}
        text={intl.formatMessage({ id: "bonusPage.medals.text" })}
        callBack={handleOpenModal}
        classList={{
          root: css.rakeRoot,
          wrapper: css.wrapper,
          title: css.title,
          text: css.text,
          link: css.link,
        }}
      />
      <div className={css.backgroundOverlay}>
        <img
          className={css.img}
          src={isMobile ? medalsMobileImg : medalsImg}
          alt=""
        />
      </div>
    </div>
  );
};

export default Medal;
