import React, { FC } from "react";
import { useGetLocale } from "providers";
import clsx from "clsx";

import css from "./style.module.scss";

interface Props {
  title: string;
  text: string;
  textColor?: string;
  wrap?: boolean;

  callBack?: () => void;

  classList?: {
    link?: string;
    root?: string;
    wrapper?: string;
    title?: string;
    text?: string;
  };
}

const DataBlock: FC<Props> = ({
  text,
  title,
  textColor,
  wrap,
  callBack,
  classList,
}) => {
  const intl = useGetLocale();

  const textStyle = {
    color: textColor || "",
  };

  const handleClick = () => {
    if (callBack) {
      callBack();
    }
  };

  return (
    <div
      className={clsx(
        css.root,
        {
          [css.wrapRoot]: wrap,
        },
        {
          [classList?.root || ""]: !!classList?.root,
        },
      )}>
      <div className={clsx(css.textWrapper, classList?.wrapper)}>
        <h1 className={clsx(css.title, classList?.title)}>{title}</h1>
        <p className={clsx(css.text, classList?.text)} style={textStyle}>
          {text}
        </p>
      </div>
      <p
        className={clsx(css.link, {
          [classList?.link || ""]: !!classList?.link,
        })}
        onClick={handleClick}>
        {intl.formatMessage({ id: "bonusPage.details" })}
      </p>
    </div>
  );
};

export default DataBlock;
